<script>
import PatternTitle from "@/components/PatternTitle.vue";
// @ is an alias to /src
export default {
  name: "pricing-page",
  data() {
    return {
      title: "pricing",
    };
  },
  components: {
    PatternTitle,
  },
  methods: {
    showEnswer() {
      const qs = Array.from(document.querySelectorAll(".question"));
      const as = Array.from(document.querySelectorAll(".answer"));
      qs.forEach((q) => {
        q.addEventListener("click", function () {
          for (let i = 0; i < qs.length; i++) {
            qs[i].classList.remove("active");
          }
          for (let i = 0; i < as.length; i++) {
            as[i].classList.add("hide");
          }
          this.classList.add("active");
          this.nextElementSibling.classList.remove("hide");
        });
      });
    },
  },
};
</script>
