<template>
  <div class="home">
    <div class="boxes">
      <div
        :class="[box.class, 'box']"
        :data-id="box.id"
        v-for="box in boxes"
        :key="box.id"
      >
        <h1>{{ box.title }}</h1>
        <p>{{ box.text }}</p>
        <a href="">{{ box.linkText }}</a>
      </div>
    </div>
    <div class="bullets">
      <div @click="displayMessage" id="1" class="bullet active"></div>
      <div @click="displayMessage" id="2" class="bullet"></div>
      <div @click="displayMessage" id="3" class="bullet"></div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: "HomeView",
  data() {
    return {
      boxes: [
        {
          id: 1,
          title: "Welcome to Company",
          text: "Ut velit est quam dolor ad a aliquid qui aliquid. Sequi ea ut et est quaerat sequi nihil ut aliquam. Occaecati alias dolorem mollitia ut. Similique ea voluptatem. Esse doloremque accusamus repellendus deleniti vel. Minus et tempore modi architecto.",
          linkText: "read more",
          class: "",
        },
        {
          id: 2,
          title: "Lorem Ipsum Dolor",
          text: "Ut velit est quam dolor ad a aliquid qui aliquid. Sequi ea ut et est quaerat sequi nihil ut aliquam. Occaecati alias dolorem mollitia ut. Similique ea voluptatem. Esse doloremque accusamus repellendus deleniti vel. Minus et tempore modi architecto.",
          linkText: "read more",
          class: "hide",
        },
        {
          id: 3,
          title: "Sequi ea ut et est quaerat",
          text: "Ut velit est quam dolor ad a aliquid qui aliquid. Sequi ea ut et est quaerat sequi nihil ut aliquam. Occaecati alias dolorem mollitia ut. Similique ea voluptatem. Esse doloremque accusamus repellendus deleniti vel. Minus et tempore modi architecto.",
          linkText: "read more",
          class: "hide",
        },
      ],
    };
  },
  methods: {
    displayMessage() {
      const bullets = Array.from(document.querySelectorAll(".bullet"));
      const allBoxes = Array.from(document.querySelectorAll(".box"));
      bullets.forEach((b) => {
        b.addEventListener("click", function () {
          for (let i = 0; i < bullets.length; i++) {
            bullets[i].classList.remove("active");
          }
          allBoxes.forEach((box) => {
            if (box.dataset.id == this.id) {
              box.classList.remove("hide");
            } else {
              box.classList.add("hide");
            }
          });
          this.classList.add("active");
        });
      });
    },
  },
};
</script>
